#labortimetable {
  height: 86%;
}
.xieck_changepage {
  position: fixed;
  width: 100%;
  top: 40%;
  left: 0;
}
.xieck_changepage > span {
  background-color: rgba(0, 0, 0, 0.5);
  padding: .2rem .02rem;
}
.xieck_changepage > span > img {
  height: .8rem;
}
.xieck_pageleft {
  float: left;
  border-radius: 0 .1rem .1rem 0;
}
.xieck_pageright {
  float: right;
  border-radius: .1rem 0 0 .1rem;
}
ul.box li {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border-bottom: 1px solid #ddd;
  margin: 0.2rem;
  padding: 0.1rem;
}
ul.box li span:first-child {
    width: 1.2rem;
}
.teacherlistinfo {
  padding: 0 0.2rem;
  font-size: 0.2rem;
}
.teacherlistinfo ul {
    border: 1px solid #eee;
    margin-bottom: 0.2rem;
}
.teacherlistinfo ul li {
      border: none;
      padding: 0.1rem;
}
.teacherlistinfo ul li span:first-child {
        width: 1.2rem;
        margin-left: 0.5rem;
}
.studenttable {
  width: 90%;
  margin: 0.2rem auto;
}
.studenttable :first-child {
    width: 30%;
}
.studenttable :last-child {
    width: 30%;
}
.studenttable tr {
    text-align: center;
    height: 0.5rem;
}



