
.attentionbtn{
	border: 1px solid;
	border-radius: 50%;
	-webkit-transform: rotate(180deg);
	        transform: rotate(180deg);
	width: 0.3rem;
	height: 0.3rem;
	cursor: pointer;
	margin-left: 0.4rem;
}
.attention{
	background-color: rgba(0,0,0,0.5);
	position: fixed;
	top: 0;
	width: 100%;
	height: 100%;
}
.attentioncontent{
	background-color: #fff;
	position: absolute;
	bottom: 0;
	height: 85%;
	width: 100%;
}
.attentiontitle{
	text-align: center;
	padding: .4rem 0 .5rem 0;
	font-size: .4rem;
}
.attentiontext{
	padding: 0 .2rem;
	text-align: justify;
}
.close{
	-webkit-transform: rotate(45deg);
	        transform: rotate(45deg);
	position: absolute;
	font-size: 1rem;
	right: .3rem;
	top: 0.05rem;
}
.ld_add .advice{
	outline: 1px solid lightgray;
	margin-bottom: .2rem;
	width: 80%;
	height: 2rem;
	margin-left: 10%;
}



