
.statistics {
    padding: 0 0.2rem;
    background: #fff;
    font-size: 0;
    margin-bottom: 0.2rem;
}
.title {
    padding-top:0.2rem;
    color: #3499db;
    width: 100%;
    display: block;
	font-size: 0.267rem;
	margin: 0 0 0.1rem 0;
}
.statistics div{
}
.staboxleft{
	display: inline-block;
	width: 35%;
	font-size: 0.267rem;
	position: relative;
	text-align: center;
	vertical-align: middle;
}
.staboxleft p{
	position: absolute;
	top: 33%;
	width: 100%;
	color: #fff;
	font-size: 0.5rem;
}
.staboxleft p span{
	font-size: 0.25rem;
}
.staboxleft img{
	width: 100%;
}
.staboxright{
	display: inline-block;
	width: 65%;
	font-size: 0.267rem;
	vertical-align: middle;
}
.staboxright div{
	line-height: 0.6rem;
}
.staboxright img{
	width: 0.35rem;
	vertical-align: middle;
	margin: 0 0.2rem;
}
.staboxright span{
	vertical-align: middle;
	line-height: 0.35rem;
}
.content {
    height:4rem;
    padding:0.2rem;
    background:#fff;
}
#echart {
        width: 5rem;
        height: 4rem;
        margin: auto;
}
.err {
	font-size: 0.267rem;
}
.ldpoints {
    width:100%;
    margin:0.2rem auto;
}
.ldpoints tr{
	text-align: center;
	height: 0.5rem;
}
.ldpoints th{
	font-weight: 100;
	color: #3499db;
}
.ldpoints td{
	color: #333;
	font-weight: 100;
	text-align: center;
}



