
.my-exam-list .item {
  padding: .25rem;
  background: #FFF;
  margin-bottom: .3rem;
  position: relative;
}
.my-exam-list .item:after {
    display: block;
    content: ' ';
    position: absolute;
    top: 0;
    right: 0;
    width: 1.1rem;
    height: 1.1rem;
}
.my-exam-list .item.my-exam-executing:after {
    background: url("../../../assets/images/tips_jxz.png");
    background-size: cover;
    background-repeat: no-repeat;
}
.my-exam-list .item.my-exam-notbegin:after {
    background: url("../../../assets/images/tips_swks.png");
    background-size: cover;
    background-repeat: no-repeat;
}
.my-exam-list .item.my-exam-end:after {
    background: url("../../../assets/images/tips_yjs.png");
    background-size: cover;
    background-repeat: no-repeat;
}
.my-exam-list .item.my-exam-miss:after {
    background: url("../../../assets/images/tips_qk.png");
    background-size: cover;
    background-repeat: no-repeat;
}
.my-exam-list .item p {
    line-height: 1.5;
    font-size: 0.24rem;
}
.my-exam-list .item p label {
      color: #888;
}



