
.booking-time-sel {
  border-top: 0;
}
.booking-time-sel .time-sel {
    background: #FFF;
    margin-bottom: .2rem;
    padding: .24rem;
}
.booking-time-sel .place {
    font-size: .28rem;
    font-weight: 400;
}
.booking-time-sel .lab-info {
    font-size: .26rem;
    margin-bottom: .24rem;
    color: #333;
}
.booking-time-sel .lab-info .view-lab {
      font-size: .2rem;
      color: #369;
}
.booking-time-sel .time-tabs {
    border: 1px solid #3499db;
    border-radius: .06rem;
    padding-left: .18rem;
    padding-right: .18rem;
}
.booking-time-sel .time-tabs .tab label {
      /*color: #888;*/
      font-size: .26rem;
      padding: .25rem .05rem .2rem .05rem;
}
.booking-time-sel .btn {
    font-size: .26rem;
}
.booking-time-sel .lab-info-table {
    width: 100%;
}
.booking-time-sel .lab-info-table tr th, .booking-time-sel .lab-info-table tr td {
      padding: .2rem;
      font-weight: normal;
      text-align: center;
      border-right: 1px solid #c3c3c3;
      border-bottom: 1px solid #c3c3c3;
}
.booking-time-sel .lab-info-table tr th:last-child, .booking-time-sel .lab-info-table tr td:last-child {
        border-right: none;
}
.booking-time-sel .lab-info-table tr td {
      font-size: 0.24rem;
      color: #666;
}
.booking-time-sel .lab-info-table tr th {
      width: 50%;
      padding: .25rem;
      color: #333;
}
.booking-time-sel .lab-info-table tr:last-child td {
      border-bottom: none;
}



