
@charset "UTF-8";
.s_fin dl > dd {
  margin-bottom: .14rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.s_fin .show {
  color: #3499db;
}
.s_fin dd .d-left {
  width: 1.7rem;
  color: #747474;
  display: inline-block;
}
.s_fin dd span:nth-child(2) {
  color: #4c4c4c;
}
.s_fin dd .d-special {
  color: black;
  font-weight: 400;
}
.s_fin .tgtp,
.btgtp {
  width: 1.1rem;
}
.s_fin .d_last {
  margin-bottom: -.4rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border-top: 1px solid lightgray;
}
.s_fin .d_last .d-left {
  line-height: 1rem;
}
.s_fin .d_last .d_right {
  margin-top: .13rem;
  padding-top: .2rem;
  width: 62%;
  display: inline-block;
  word-break: break-all;
}
.s_fin .nav-top {
  height: .9rem;
  width: 100%;
  background-color: #F8F8F8;
  line-height: .8rem;
  padding-left: .2rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-right: .2rem;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  position: fixed;
  top: 1.48rem;
  z-index: 1000000;
}
.s_fin .nav-top p {
  font-size: .22rem;
  cursor: pointer;
  line-height: 1rem;
}

/***************下拉框********************/
.select_box {
  width: 100%;
  height: auto;
  box-sizing: border-box;
  position: fixed;
  top: 1.48rem;
  background-color: #fff;
  z-index: 10;
}
.s_fin .el-select {
  text-align: center;
  display: inline-block;
  width: 32.2%;
}
.s_fin .el-input__inner {
  border: 1px solid #c0ccda;
}
.s_fin .el-select-dropdown__list {
  max-height: .8rem;
}
.s_fin .el-select-dropdown__item {
  font-size: .24rem;
  height: .6rem;
}
.el-input--mini .el-input__inner {
  height: .5rem !important;
}
.el-select-dropdown__item {
  font-size: .2rem !important;
  height: .5rem !important;
}
.el-select .el-input .el-input__icon {
  font-size: .2rem !important;
  width: auto !important;
}



