
.result_d {
  padding-bottom: 4rem;
}
.result_d ul {
    background: #fff;
    padding: 0.3rem;
    margin-bottom: 0.1rem;
}
.result_d ul li {
      text-align: left;
      line-height: 0.5rem;
      font-size: 0.3rem;
      color: #888;
}
.result_d ul li span.teacher {
        color: #888;
}
.result_d ul :nth-child(1) {
      color: #000;
}
.open {
  background: #eee;
  width: 100%;
  position: fixed;
  bottom: 0;
}
.open .door_btn {
    width: 2rem;
    height: 2rem;
    margin: 1rem auto;
    color: #fff;
    background: url("../../assets/images/signbtns.png") 0 0 no-repeat;
    background-size: cover;
}
.open .door_btn p {
      display: inline-block;
      position: relative;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%);
      font-size: 0.35rem;
}
li.door_name {
  line-height: 0.7rem;
  text-align: center;
  border-bottom: 1px solid #999;
}



